<template>
  <div id="app">
    <ul class="container">
      <li>
        <h2></h2>
        <rudy-scrollbar width="600px" height="300px" :noresize="true">
          <h3>,
            
            
            
            
            noresize="true" 
            img
            noresize=trueelementUI
            </h3>
          <div>noresize="true"<br>
          <br>
          noresize</div>
          <div style="color: red;">Vue.use() <br>&lt;rudy-scrollbar&gt;  &lt;rudy-scrollbar&gt;  </div>
          <ul class="description" style="height: 600px">
            <li class="title"></li>
            <li>width <em></em> <span>String :100px/100em/10%</span></li>
            <li>height <em></em> <span>String :100px/100em/10%</span></li>
            <img src="./images/oneone.jpg" alt="">
          </ul>
        </rudy-scrollbar>
      </li>
      <li>
        <h2></h2>
        <rudy-scrollbar max-width="600px" max-height="300px" :noresize="true">
          <h3></h3>
          <ul class="description" style="height: 600px;">
            <li class="title"></li>
            <li>max-width <em></em> <span>String :100px/100em/10%</span></li>
            <li>max-height <em></em> <span>String :100px/100em/10%</span></li>
            <img src="./images/twotwo.jpg" alt="" style="width: 1000px;height:auto">
          </ul>
        </rudy-scrollbar>
      </li>
      <li>
        <h2></h2>
        <rudy-scrollbar width="600px" height="300px" :transition="true">
          <h3></h3>
          <ul class="description" style="height: 600px">
            <li class="title"></li>
            <li>transition <em></em> <span>Boolean false</span></li>
            <img src="./images/threethree.jpg">
          </ul>
        </rudy-scrollbar>
      </li>
      <li>
        <h2></h2>
        <rudy-scrollbar width="600px" height="300px" :lastingShowBar="true" :transition="true">
          <h3></h3>
          <ul class="description" style="height: 600px; width: 1000px">
            <li class="title"></li>
            <li>lastingShowBar <em></em> <span>Boolean false</span></li>
            <img src="./images/fourfour.jpg">
          </ul>
        </rudy-scrollbar>
      </li>
      <li>
        <h2></h2>
        <scrollbar ref="scrollbar" width="600px" height="300px" :transition="true">
          <h3>xy</h3>
          <ul class="description" style="height: 600px; width: 1000px">
            <li class="title"></li>
            <li>scrollTop <em></em> <span>Number :100</span></li>
            <li>scrollLeft <em></em> <span>Number :100</span></li>
            <li>scrollTo <em></em> <span>Number x:100,y:100</span></li>
            <img src="./images/five1.jpg">
            <img src="./images/five2.jpg">
          </ul>
        </scrollbar>
        <hr>  
        <div style="text-align: center">
          <button @click="targetPosition">button</button> x:
          <input v-model="xxx" type="text"> y:
          <input v-model="yyy" type="text">
        </div>
      </li>
      <li>
        <h2></h2>
        <rudy-scrollbar width="600px" height="300px" :transition="true" @scroll="scrollHandle">
          <h3></h3>
          <span style="backround-color: #ffffff; position: absolute; right: 10px; top: 5px; color: red;">x:{{this.x}} y:{{this.y}}</span>
          <ul class="description" style="height: 600px; width: 1000px;">
            <li class="title"></li>
            <li>scroll <em></em> <span> event </span></li>
            <img src="./images/six1.jpg" alt="">
            <img src="./images/six2.jpg" alt="">
          </ul>
        </rudy-scrollbar>
      </li>
      <li>
        <h2></h2>
        <rudy-scrollbar width="600px" height="300px" :transition="true">
          <h3></h3>
          <ul class="description" style="height: 600px; width: 1000px;">
            <li class="title"></li>
            <li>
              <img src="./images/seven1.jpg" alt="">
              <img src="./images/seven2.jpg" alt="">
            </li>
          </ul>
        </rudy-scrollbar>
      </li>
      <li>
        <h2>elmentUIbug</h2>
        <div>
          <ul>
            <li>1. ondragstart thumbmouseup</li>
            <li>2.destroyed hook  </li>
            <br>
            <li style="font-weight: bold; color: red;">DOMUI</li>
          </ul>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

import scrollbar from '../libs/scrollbar/index.js'

// 

export default {
  name: 'app',
  components: {scrollbar},
  data () {
    return {
      msgs: "Irish News. Garda and family 'very concerned' for man missing since early hours of today. Garda are appealing for help in tracing a missing man in Co Wexford",
      xxx: 0,
      yyy: 0,
      x: 0,
      y: 0
    }
  },
  methods: {
    targetPosition() {
      this.$refs.scrollbar.scrollTo(this.xxx, this.yyy)
    },
    scrollHandle(e) {
      this.x = e.target.scrollLeft
      this.y = e.target.scrollTop
      console.log(e)
    }
  }
}
</script>

<style lang="stylus">
.container
  height 100%
  min-width 1200px
  > li
    width calc(100% / 2)
    float left
    height 400px
    h2
      text-align center
    h3
    h4
      width 550px
      margin 5px
  .el-scrollbar
    background-color #ffffff
    border 1px solid #000000
    margin 10px auto 0
  .el-scrollbar__view
    padding 10px
    box-sizing border-box
  .description
    padding-left 10px
    background-image linear-gradient(45deg, pink, rgba(0, 0, 0, .1))
    background-size 20px 20px
    li.title
      font-weight bold
    li:not(.title)
      padding-left 10px
    li
      color #000000
      em
        color #666666
      span 
        color blue
</style>



